<template>
  <scroll id="tabmenu">
    <div class="mebu-list">
      <div
        class="menu-list-item"
        :class="{active: index==currentIndex}"
        v-for="(item, index) in categories"
        :key="index"
        @click="itemClick(index)"
      >
        {{item.title}}
      </div>
    </div>
  </scroll>
</template>

<script>
import Scroll from "components/common/scroll/Scroll";
export default {
  components: { Scroll },
  name: "TabMenu",
  compoents: {
    Scroll,
  },
  props: {
    categories: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data(){
      return {
          currentIndex: 0
      }
  },
  methods: {
      itemClick(index){
          this.currentIndex = index
          this.$emit('selectItem',index)
      }
  }
};
</script>

<style scoped>
#tabmenu {
    height: 100%;
    width: 100px;
    background-color: #f6f6f6;
    box-sizing: border-box;
}

.menu-list-item {
    height: 45px;
    font-size: 14px;
    line-height: 45px;
    text-align: center;
}
.menu-list-item.active {
    font-weight: 700;
    color: var(--color-high-text);
    background-color: #fff;
    border-left: 3px solid var(--color-high-text);
}
</style>